.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 100px;
    position: fixed;
    right: 5px;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100px;
  display: flex;
  flex-direction: row;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

  .App-header h1 {
    margin: 0 20px;
    font-family: monospace;
  }

.App-link {
  color: #61dafb;
}

.Sign-in-button-div {
  margin: 200px;
}
.Sign-in-button-div button {
  font-size: 30px;
}

.Google-photo {
  border-radius: 20px;
  height: 70px;
  margin-left: 40px;
}

.App-header label {
  margin-left: 40px;
  background: #555;
  padding: 0 20px;
  border-radius: 10px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
